<script>
export default {
  name: '',
  props: {},
  data() {
    return {
      active_menu_cars_park: true,
      active_menu_cars: false,
      active_menu_drivers: false,
      active_menu_travels: false,
      active_menu_groups: false,

      active_menu_pay: true,
      active_menu_auto_pay: false,
      active_menu_req_pay: false,

      active_menu_rept: true,
      active_menu_pay_report: false,
      active_menu_debit_report: false,
      active_menu_bill: false,

      active_menu_setting: true,
      active_menu_setting_global: false,
      active_menu_users: false,
      active_menu_agregators: false,
      active_menu_banks: false,
      active_menu_balances: false,
      active_menu_logs: false,

      active_menu_profile: false,

      hide_elem: false,
    };
  },
  mounted() {
    this.check_page();
  },
  methods: {
    show_hide_sidebar() {
      this.hide_elem = !this.hide_elem;
    },
    show_obj_menu_cars_park() {
      this.active_menu_cars_park = !this.active_menu_cars_park;
    },

    show_obj_menu_pay() {
      this.active_menu_pay = !this.active_menu_pay;
    },
    show_obj_menu_rept() {
      this.active_menu_rept = !this.active_menu_rept;
    },

    show_obj_menu_setting() {
      this.active_menu_setting = !this.active_menu_setting;
    },

    show_obj_menu_profile() {
      this.active_menu_profile = !this.active_menu_profile;
    },

    check_page() {
      const pagesCarsPark = ['drivers-page', 'cars-page', 'travels-page', 'groups-page'].indexOf(
        this.$route.name,
      );
      const pagesPay = ['pay-auto-page', 'pay-req-page', 'bill-page'].indexOf(this.$route.name);
      const pagesSettings = ['settings-page', 'users-page', 'agregators-page', 'banks-page', 'balances-page', 'logs-page'].indexOf(
        this.$route.name,
      );
      const pagesReport = ['payment-report-page', 'debiting-report-page'].indexOf(
        this.$route.name,
      );

      if (pagesCarsPark === 0) {
        this.active_menu_drivers = true;
      }
      if (pagesCarsPark === 1) {
        this.active_menu_cars = true;
      }
      if (pagesCarsPark === 2) {
        this.active_menu_travels = true;
      }
      if (pagesCarsPark === 3) {
        this.active_menu_groups = true;
      }
      if (pagesCarsPark === -1) {
        this.active_menu_cars_park = false;
      }

      if (pagesPay === 0) {
        this.active_menu_auto_pay = true;
      }
      if (pagesPay === 1) {
        this.active_menu_req_pay = true;
      }
      if (pagesPay === 2) {
        this.active_menu_bill = true;
      }
      if (pagesPay === -1) {
        this.active_menu_pay = false;
      }

      if (pagesSettings === 0) {
        this.active_menu_setting_global = true;
      }
      if (pagesSettings === 1) {
        this.active_menu_users = true;
      }
      if (pagesSettings === 2) {
        this.active_menu_agregators = true;
      }
      if (pagesSettings === 3) {
        this.active_menu_banks = true;
      }
      if (pagesSettings === 4) {
        this.active_menu_balances = true;
      }
      if (pagesSettings === 5) {
        this.active_menu_logs = true;
      }
      if (pagesSettings === -1) {
        this.active_menu_setting = false;
      }

      if (pagesReport === 0) {
        this.active_menu_pay_report = true;
      }
      if (pagesReport === 1) {
        this.active_menu_debit_report = true;
      }
      if (pagesReport === -1) {
        this.active_menu_rept = false;
      }

    },
  },
};
</script>

